import React from 'react';
import ApiService from './API';
import Popoutwindow from './popoutwindow'
import {Table,Card,Form,Button,Jumbotron,Container,Badge} from 'react-bootstrap';
import img_lotto from"./lotto_boll.jpg"
import Infs from "./Infs"

class  ActionBet extends React.Component {
    constructor(props) {
        // Inherit constructor
        super(props);
        // State for form data and error message
        
        // Bind functions
        this.state={
            pop_show:false,
            pop_console:'',
            returnobj:{passinf:'Loading'},
            current_balance_rerender:true,
        }
        
        this.handleSubmit = this.handleSubmit.bind(this);
    }
    handleSubmit(event) {
        
        event.preventDefault();
        const { form } = this.props.state;
        const memodata = 'bet:'.concat(form.numbers,':',form.period);
        this.setState({returnobj:{passinf:'Loading'},pop_show:true})
        
        return ApiService.bet(memodata)
            .then((mesg) => {
                console.dir(mesg);
                this.setState({returnobj:mesg.processed,current_balance_rerender:true})
                this.props.reset_userinf();
                
            })
            .catch(err => {
                console.dir(err);
                console.dir(memodata);
                this.setState({returnobj:{passinf:'error',errormessage:err.message}})
                this.props.reset_userinf();
            });
    }

    render(){
        if(! sessionStorage.getItem("User_EOS_key")){
            return(
                <p>please set user</p>
            );
        }
        else{
            const { form, error } = this.props.state;
            return(
                <>
                <div className="w80 mc upp">{titleblocks}</div>
                
                <div id="bet_block" className="w50 mc upp">
                <Popoutwindow show={this.state.pop_show} title={"Transaction Information"} handleClose={()=>this.setState({pop_show:false})} console_obj={this.state.returnobj}/>
                <Form onSubmit={this.handleSubmit}>
                    <Form.Group >
                        <Form.Label>Numbers</Form.Label>
                        <Form.Control name="numbers" type="text" placeholder="1~39 ,ex:1,2,3,4,5" autoComplete="off" onChange={this.props.handleChange} value={form.numbers}/>
                        <Form.Text className="text-muted">
                            chose 5 numbers in 1~39,and split it by ','
                        </Form.Text>
                    </Form.Group>

                    <Form.Group controlId="formBasicPassword">
                        <Form.Label>Period</Form.Label>
                        <Form.Control name="period" type="number" placeholder="period" autoComplete="off" onChange={this.props.handleChange} value={form.period}/>
                    </Form.Group>
                    
                    <Button variant="primary" type="submit">
                        Submit
                    </Button>
                </Form>
                </div>
                <div className="w60 mc up4p">{infblock}</div>
                <Infs current_balance_rerender={this.state.current_balance_rerender} rended={()=>this.setState({current_balance_rerender:false})} rerend={()=>this.setState({current_balance_rerender:true})}/>
                </>
            );
        }
    }
}
const bgGround_lotto= {
    backgroundImage: 'url(' +img_lotto + ')'//圖片的路徑
  };
const titleblocks=(
    
    <Jumbotron fluid className="bg-dark text-white" style={bgGround_lotto}>
        <Container className=" w80">
            <h1 className="ar">Buy numbers</h1>
            <div className="frow mb">
            
            <p className="mb"></p><h3 className="mb">Chose<Badge variant="danger">5</Badge>{' '}</h3><p className="mb"></p> <h3 className="mb">numbers from <Badge variant="warning">1~39</Badge>{' '}</h3>
            
            </div>
            
    </Container>
    </Jumbotron>
    
)
const infblock=(
    <>
    <h3 className="dwp"> <Badge pill variant="primary">How to play</Badge>{' '}</h3>
    <Card body className="bg-dark text-white">You must to chose 5 numbers from 1 to 39 ,for example :"1,2,3,4,5". When the lottery is drawn, the contract will set five numbers as prize numbers for the current period. Among your five numbersif there are more than two (including two numbers) that match the five numbers drawn in the current period, you will win the prize, and you can redeem the prize and it will send token to your account. </Card>
    <h3 className="up4p dwp"> <Badge pill variant="info">Bonus Distribution</Badge>{' '}</h3>
    <Table responsive className="bg-dark text-white">
        <thead><th>Prize you win</th><th>First prize</th><th>Second prize</th><th>Third prize</th><th>Fourth prize</th></thead>
        <thead><th>Odds</th><th>48000.0</th><th>1200.0</th><th>18.0</th><th>3.0</th></thead>
    </Table>
    </>
    )
export default ActionBet;