import { Nav,Button,Modal} from 'react-bootstrap';
import React from 'react';
import {GetUserInf}from './Action';


class TopNav  extends React.Component {
    constructor(props){
        super(props);
        
    }
    setinsetuser(params){
        this.props.setuserin(params);
    }
   
    render(){
        return(
            <>
            
            <Nav className="justify-content-center"  variant="tabs">
                
                    
                
                <Nav.Item >
                    <Nav.Link onClick={this.setinsetuser.bind(this,"setuser")} >{"SET USER "}</Nav.Link>   
                </Nav.Item>
                <Nav.Item >
                    <Nav.Link onClick={this.setinsetuser.bind(this,"About")}>{"About "}</Nav.Link>
                </Nav.Item>
                <Nav.Item>
                    <Nav.Link onClick={this.setinsetuser.bind(this,"bet")}>{"BET "}</Nav.Link>
                </Nav.Item>
                <Nav.Item>
                    <Nav.Link onClick={this.setinsetuser.bind(this,"redeem")}>{"REDEEM "}</Nav.Link>
                </Nav.Item>
                <Nav.Item>
                    <Nav.Link onClick={this.setinsetuser.bind(this,"mytickets")}>{"My Tickets "}</Nav.Link>
                </Nav.Item>
                <Nav.Item>
                    <Nav.Link onClick={this.setinsetuser.bind(this,"home")}>{"Home "}</Nav.Link>
                </Nav.Item>
                
              </Nav>
              </>
        );
    }
    
}




export default TopNav;