import { Nav,Button,Modal,Spinner} from 'react-bootstrap';
import React from 'react';
import {Table,Card,Form,Jumbotron,Container,Badge} from 'react-bootstrap';
import ApiService from './API';

class ActionCreate extends React.Component {
    
    constructor(props){
        super(props);
        this.state = {
            form: {
                username: '',
                key: '',
                error: '',
                },
            usercreate:0,
        }
        this.handleChange=this.handleChange.bind(this)
        this.handleCreate=this.handleCreate.bind(this)
    }
    handleChange(event) {
        const { name, value } = event.target;
        const { form } = this.state;

        this.setState({
            form: {
                ...form,
                [name]: value,
                error: '',
            },
        });
    }
    handleCreate(event){
        event.preventDefault();
        
        
        this.setState({usercreate:1});
        
        return ApiService.createaccount(this.state.form.username,this.state.form.key)
            .then((mesg) => {
                console.dir(mesg);
                this.setState({usercreate:2})
                
            })
            .catch(err => {
                console.dir(err);
                
                this.setState({usercreate:3})
                
            });
    }
    
    
  
    render(){
    let contain=(<></>)
    if(this.state.usercreate==0){
        contain=(
            <Form name="form" onSubmit={this.handleCreate}>
                        <Form.Text>
                           The account must be create by another account, please make sure
                           you had set user before in this session. 
                        </Form.Text>
                      <Form.Group >
                      
                          <Form.Label>Account name</Form.Label>
                          <Form.Control  name="username" type="text" placeholder="All small letters, a-z, 1-5 or dot, max 12 characters" onChange={this.handleChange} pattern="[\.a-z1-5]{2,12}" required autoComplete="off"/>
                      </Form.Group>
                      <Form.Group >
                          <Form.Label>Public key</Form.Label>
                          <Form.Control name="key" type="password" value={this.state.form.key} onChange={this.handleChange} pattern="^.{51,}$" required autoComplete="new-password"/>
                      </Form.Group>
                      <Button variant="primary" type="submit">
                          Create
                      </Button>{' '}
                      <a href='https://eosauthority.com/generate_eos_private_key'>key generator</a>     
                  </Form>
          )
    }else if(this.state.usercreate==1){
        contain=(
            <Spinner animation="border" role="status">
            <span className="sr-only">Loading...</span>
          </Spinner>
        )
    }
    else if(this.state.usercreate==2){
        contain=(
            <h1>create successes</h1>
        )
    }
    else{
        contain=(
            <h1>error</h1>
        ) 
    }
        
    return (
      <>
        <Modal show={this.props.show} onHide={this.props.handleClose}>
          <Modal.Header closeButton>
            <Modal.Title>{this.props.title}</Modal.Title>
          </Modal.Header>
          <Modal.Body>{contain}</Modal.Body>
          <Modal.Footer>
            <Button variant="primary" onClick={this.props.handleClose}>
            Close
            </Button>
          </Modal.Footer>
        </Modal>
      </>
    );
  }
}

export default ActionCreate;