import React from 'react';
import ApiService from './API';

const Button = ({ ...props }) => {
  const { className, type, style, onClick, children } = props;
  return (
    <button
      className={`Button${ className ? ' ' + className : '' }`}
      { ...{ type, style, onClick } }
    >{ children }</button>
  );
}


class ActionBlock extends React.Component {
    constructor(props) {
        // Inherit constructor
        super(props);
        // State for form data and error message
        
        // Bind functions
        
        this.handleSubmit = this.handleSubmit.bind(this);
    }
    
    handleSubmit(event) {
        // Stop the default form submit browser behaviour
        event.preventDefault();
        // Extract `form` state
        const { form } = this.props.state;
        const setCosoleString=(params) => this.props.setConsole(params);
        // Extract `setUser` of `UserAction` and `user.name` of UserReducer from redux
        //const { setUser } = this.props;
        console.dir("dhfkajshfljashfla");
        sessionStorage.setItem("User_EOS_key", form.key);
        sessionStorage.setItem("User_EOS_account", form.username);
        sessionStorage.setItem("User_EOS_numbers", form.numbers.concat(':',form.period));
        // Send a login transaction to the blockchain by calling the ApiService,
        // If it successes, save the username to redux store
        // Otherwise, save the error state for displaying the message
        this.props.setConsole("waiting~~");
        return ApiService.bet(setCosoleString)
            .then(() => {
                console.dir("");
                
            })
            .catch(err => {
                setCosoleString("some thing was wrong please try it again")
            });
      }
    render() {
        const { form, error } = this.props.state;
        return (
            <form name="form" onSubmit={this.handleSubmit}>
                <div className="field" id="betnumbers">
                    
                    <input
                        type="text"
                        name="numbers"
                        value={form.numbers}
                        placeholder="1-39 ex:1,2,3,4,5"
                        onChange={this.props.handleChange}
                        required
                        autoComplete="off"
                    />
                </div>
                <div className="field" id="period">
                    
                    <input
                        type="number"
                        name="period"
                        value={form.period}
                        placeholder="period"
                        onChange={this.props.handleChange}
                        required
                        autoComplete="off"
                    />
                </div>
                <div className="fieldform-error">
                    {error && <span className="error">{error}</span>}
                </div>
                <div className="bottom">
                    <Button type="submit" className="green">
                        {"BET"}
                    </Button>
                    
                </div>
            </form>
        );
    }

}

export default ActionBlock;
