import React from 'react';
import ApiService from './API';

class ActionBlock2 extends React.Component{
    constructor(props){
        super(props);
        this.handleSubmit=this.handleSubmit.bind(this);
    }
    handleSubmit(event) {
        // Stop the default form submit browser behaviour
        event.preventDefault();
        // Extract `form` state
        const { form } = this.props.state;
        const setCosoleString=(params) => this.props.setConsole(params);
        // Extract `setUser` of `UserAction` and `user.name` of UserReducer from redux
        //const { setUser } = this.props;
        console.dir("dhfljashfla");

        // Send a login transaction to the blockchain by calling the ApiService,
        // If it successes, save the username to redux store
        // Otherwise, save the error state for displaying the message
        this.props.setConsole("waiting~~");
        return ApiService.open(form.gameid)
            .then(() => {
                console.dir("dhfljtututuhfla");
            })
            .catch(err => {
            });
      }
    render(){
        const { form, error } = this.props.state;
        return(
            <form name="form" onSubmit={this.handleSubmit}>
                <div className="field" >
                    
                    <input
                        type="number"
                        name="gameid"
                        value={form.gameid}
                        placeholder="in put ticket ID"
                        onChange={this.props.handleChange}
                        pattern="[\.0-9]"
                        required
                        autoComplete="off"
                    />
                </div>
                <div className="bottom">
                    <Button type="submit" className="green">
                        {"redeem"}
                    </Button>
                </div>
            </form>
        );
    }
}
const Button = ({ ...props }) => {
    const { className, type, style, onClick, children } = props;
    return (
      <button
        className={`Button${ className ? ' ' + className : '' }`}
        { ...{ type, style, onClick } }
      >{ children }</button>
    );
  }

export default ActionBlock2;
