import React from 'react';
import ApiService from './API';
import img_fa from './fa.png';
import { Badge,Card,ListGroup,ListGroupItem,Button,CardDeck,Jumbotron,Container} from 'react-bootstrap';
import Popoutwindow from './popoutwindow'
import img_ticket from './lotto_boll.jpg'
const bgGround_lotto= {
    backgroundImage: 'url(' +img_ticket + ')'//圖片的路徑
  };
const titleblocks=(
    
    <Jumbotron fluid className="bg-dark text-white w80 mc" style={bgGround_lotto}>
        <Container className=" w80">
            <h1 className="al">Your ticket</h1>
            <div className="frow mb">
            
            
            </div>
            
    </Container>
    </Jumbotron>
    
)
class  ConsoleOfTable extends React.Component {
    constructor(props){
        super(props);
        this.state = {
            console_block_string:'',
            pop_show:false,
            returnobj:new Object(),
            finded:false,
            rerender:false,
            pass_to_obj:{passinf:'Loading'},
        }
        this.handel_look=this.handel_look.bind(this);
        ApiService.get_ticket_table()
            .then((mesg) => {
                console.dir(mesg);
                
                //this.setState({console_block_string:JSON.stringify(mesg.rows)})
                this.setState({returnobj:mesg.rows});
                this.setState({finded:true});
            })
            .catch(err => {
                console.dir(err);
                this.setState({console_block_string:"some thing was wrong please try it again"});
            });
    }
    handel_look(){
        this.setState({finded:false});
        return ApiService.get_ticket_table()
            .then((mesg) => {
            console.dir(mesg);
            
                //this.setState({console_block_string:JSON.stringify(mesg.rows)})
                this.setState({returnobj:mesg.rows});
                this.setState({finded:true});
            })
            .catch(err => {
                console.dir(err);
                this.setState({console_block_string:"some thing was wrong please try it again"});
            });
    }
    
   
    
    render(){
        if(this.state.rerender){
            this.setState({rerender:false});
            this.handel_look();
            this.props.reset_userinf();
        }
        if(this.state.finded){
            let shows = [];
            for (const item of this.state.returnobj) {
                shows.push(<SingleTicket item={item} set_pop_show={()=>this.setState({pop_show:true})} set_pass_to_obj={(params)=>this.setState({pass_to_obj:params}) } reset_userinf={this.props.reset_userinf}/>)           
            }
            return(
                <>
                {titleblocks}
                <div class="find_ticket">
                    <Popoutwindow show={this.state.pop_show} title={"Transaction Information"} handleClose={()=>this.setState({pop_show:false,rerender:true})} console_obj={this.state.pass_to_obj}/>
                    <div className="ticket_table">
                    {shows}
                    </div>
                    
                </div>
                </>
            )
        }
        else{
            return(
                <>
                {titleblocks}
                <div class="find_ticket">
                   Loading~
                   <p>{this.state.console_block_string}</p> 
                </div>
                </>
            )
        }
        

    }
    
}
class  SingleTicket extends React.Component {
    constructor(props){
        super(props);
        this.handleRedeem=this.handleRedeem.bind(this);
        this.handleRefund=this.handleRefund.bind(this);
    }
    handleRedeem() {
        const sid = parseInt(this.props.item.serialID,10);
        console.dir("redeem_from_my_ticket");
        this.props.set_pass_to_obj({passinf:'Loading'});
        this.props.set_pop_show(); 
        console.dir(sid);
        return ApiService.open(sid)
            .then((message) => {
                console.dir(message);
                this.props.set_pass_to_obj(message.processed);
                this.props.reset_userinf();
            })
            .catch(err => {
                console.dir(err);
                this.props.set_pass_to_obj({passinf:'error',errormessage:err.message});
            });
    }
    handleRefund() {
        const sid = parseInt(this.props.item.serialID,10);
        console.dir("Refund_from_my_ticket");
        this.props.set_pass_to_obj({passinf:'Loading'});
        this.props.set_pop_show(); 
        return ApiService.refund(sid)
            .then((message) => {
                console.dir(message);
                this.props.set_pass_to_obj(message.processed);
                this.props.reset_userinf();
            })
            .catch(err => {
                console.dir(err);
                this.props.set_pass_to_obj({passinf:'error',errormessage:err.message});
            });
    }
    render(){  
        const bgGround = {
            backgroundImage: 'url(' +img_fa + ')'//圖片的路徑
          };
          
        return(
            <Card  className="ticket"  bg={img_fa}>
                <Card.Img src={img_fa}/>
                <Card.ImgOverlay className="p05">
                <Card.Body className="bp0 up05">
                <Card.Title>{'ID      :'.concat(JSON.stringify(this.props.item.serialID))}</Card.Title>
                </Card.Body>
                
                <ListGroup className="list-group-flush">
                    <ListGroupItem>{''.concat(this.props.item.owner)}</ListGroupItem>
                    <ListGroupItem>{''.concat(JSON.stringify(this.props.item.numbers))}</ListGroupItem>
                    <ListGroupItem>{'Period :'.concat(this.props.item.period)}</ListGroupItem>
                    <ListGroupItem>{''.concat(this.props.item.betfunds)}</ListGroupItem>
                </ListGroup>
                <Card.Body  className="bp0 up05">
                    <Button variant="primary" onClick={this.handleRedeem}>Redeem it</Button>{' '}
                    <Button variant="danger" onClick={this.handleRefund}>Refund it</Button>
                </Card.Body>
                </Card.ImgOverlay>
                
                
                
            </Card>
            
            );
    }
}

export default ConsoleOfTable;