import React from 'react';
import ApiService from './API';

class Redeemall extends React.Component{
    constructor(props) {
        super(props)
        this.state = {
            console_block_string:'',
            returnobj:new Object(),
            finded:false,
        }
        this.redeemall=this.redeemall.bind(this);
    }
    redeemall(){
        return ApiService.redeemall(this.props.setConsole)
        .then((mesg) => {
            console.dir(mesg);
            
            //this.setState({console_block_string:JSON.stringify(mesg.rows)})
            this.setState({returnobj:mesg.rows});
            this.setState({finded:true});
        })
        .catch(err => {
            console.dir(err);
            this.setState({console_block_string:"some thing was wrong please try it again"});
        });

        
    }
    render(){
        return(
            <>
            <div class="redeemall">
            <button onClick={this.redeemall}>
                redeem all my tickets
            </button>      
            </div>
            </>
        );
        
    }
}

export default Redeemall;