import { Nav,Button,Modal,Table} from 'react-bootstrap';
import React from 'react';
import ApiService from './API';


class Infs  extends React.Component {
    constructor(props){
        super(props);
        this.state = {
            console_block_string:'',
            current_balance_string:'',
            returnobj:new Object(),
            finded:false,
            console:'History period',
            
        }
        ApiService.get_allow_table()
        .then((mesg) => {
            console.dir(mesg);
            
            //this.setState({console_block_string:JSON.stringify(mesg.rows)})
            this.setState({returnobj:mesg.rows});
            this.setState({finded:true});
            
        })
        .catch(err => {
            console.dir(err);
            this.setState({console_block_string:"some thing was wrong please try it again"});
        });
        this.de=this.de.bind(this);
        this.render_current_balance=this.render_current_balance.bind(this);
    }
    render_current_balance(){
        this.setState({current_balance_string:"Loading~"});
        ApiService.get_holder_token().then((mesg) => {
            console.dir(mesg);
            //this.setState({console_block_string:JSON.stringify(mesg.rows)})
            try {
                
                this.setState({current_balance_string:mesg.rows[0].balance});

            }catch (err){
                console.dir(err);
                this.setState({current_balance_string:"some thing was wrong "});
            }
        })
        .catch(err => {
            console.dir(err);
            this.setState({current_balance_string:"some thing was wrong "});
        });
    }


    de(){
        this.setState({console:"hi~~"});
    }


    render(){
        if(this.props.current_balance_rerender){
            this.render_current_balance();
            this.props.rended();
        }
        return(
            <div className="Infs up4p" >
                <h3 onClick={this.de}>
                    {this.state.console}
                </h3>
                <AllowTable state={this.state}/>
                <div className="upp w40 mc">
                    <h3>Timely balance</h3>
                    <Table striped bordered hover ><tr><th>Current balance</th><th>{this.state.current_balance_string}</th></tr></Table>
                </div>
            </div>
        );
    }
}
class AllowTable extends React.Component {
    constructor(props){
        super(props);
    }
    render(){
        if(! this.props.state.finded){
            return(
                <p>loading</p>
                );
        }
        if(this.props.state.finded){
        let t=[]
        for(const item of this.props.state.returnobj){
            var date = new Date(item.time*1000);
            if(item.status==0){
                t.push(
                    <tr>
                      <td>{item.period}</td>
                      <td>{date.getFullYear()}-{date.getMonth()+1}-{date.getDate()}-{date.getHours()}:{date.getMinutes()}</td>
                      <td>{JSON.stringify(item.numbers)}</td>
                      <td>{JSON.stringify(item.people_win_prize)}</td>
                      <td>{item.currenpeoplet_balance}</td>
                    </tr>
                );
            }
            else if(item.status==1){
                t.push(
                    <tr>
                        <td>{item.period}</td>
                        <td colSpan="3">Comming Soon....</td>
                        <td>unknow</td>
                    </tr>
                )
            }
            
        }
        return(
        <div className="AllowTable_table">
            <Table striped bordered hover >
              <thead>
                <tr>
                  
                  <th>Period</th>
                  <th>Time</th>
                  <th>Numbers</th>
                  <th>People Win</th>
                  <th>Balance</th>
                </tr>
              </thead>
              <tbody>
                {t}
              </tbody>
            </Table>
        </div>
        );
    }
    }
}

export default Infs;
