import React from 'react';
import ApiService from './Action/API';

import ReactDOM from 'react-dom';
import 'bootstrap/dist/css/bootstrap.min.css';
import TopNav from './TopNav'
import { About,ActionRedeem,ConsoleOfTable,SetUser,Popoutwindow,GetUserInf,Redeemall,ActionBet,Infs} from './Action';
import "./index.css";
import img_18 from "./img/18.jpg";
import img_co from "./img/co.png";
import img_fa from "./fabg.jpg";
import HomePage from "./home";

class Manage extends React.Component{
    constructor(props) {
        super(props);
        // Inherit constructor
        // State for form data and error message
        this.state = {
            form: {
                username: '',
                key: '',
                numbers:'',
                period:'',
                gameid:0,
                error: '',
                },
            console_block_string:'welcome ',
            userset:false,
            user_inf_rerand:false,
            userin:"home",
            inredeem:false,
            inbet:false,
            ininfs:false,
            inmyticket:false,
            
            pop_windo_obj:{passinf:'Loading'},
            pop_window_show:false,
        }
        this.handleChange = this.handleChange.bind(this);
        this.handleSetuser = this.handleSetuser.bind(this);
        this.wraped=this.wraped.bind(this);
        this.handleAskmoney=this.handleAskmoney.bind(this)
       
      }
    handleChange(event) {
        const { name, value } = event.target;
        const { form } = this.state;

        this.setState({
            form: {
                ...form,
                [name]: value,
                error: '',
            },
        });
    }
    handleSetuser(){
        
        this.setState({
            userset:true,
            userin:"bet",
        });
    }
    handleAskmoney(){
        this.setState({pop_windo_obj:{passinf:'Loading'}})
        this.setState({pop_window_show:true});
        return ApiService.askmoney()
        .then((mesg) => {
            console.dir(mesg);
            this.setState({pop_windo_obj:mesg.processed})
            this.setState({user_inf_rerand:true})
            
        })
        .catch(err => {
            console.dir(err);
            
            this.setState({pop_windo_obj:{passinf:'error',errormessage:err.message}})
            this.setState({user_inf_rerand:true})
        });
    }
    wraped(params) {
        
        return(
            <>
            <Popoutwindow show={this.state.pop_window_show} title={"Transaction Information"} handleClose={()=>this.setState({pop_window_show:false})} console_obj={this.state.pop_windo_obj}/>
            <div id="menu">
            <TopNav setuserin={(parms)=>this.setState({userin:parms})} />
            </div>
            <main>
                {params}
            </main>
            <GetUserInf user_inf_rerand={this.state.user_inf_rerand} reranded_inf={()=>this.setState({user_inf_rerand:false})}/>
            <div className="bg-dark text-white h40" onClick={this.handleAskmoney}>
                GIVE ME MONEY
            </div>
            </>
        );
    }

    render(){
        const { console_block_string } = this.state;
        let shows=[];
        
        switch(this.state.userin){
            case "home":
                return(this.wraped(<HomePage gosetuser={()=>this.setState({userin:"setuser"})} goabout={()=>this.setState({userin:"About"})}/>));
            case "setuser":
                return this.wraped(
                    
                        
                <div class="utlt">
                <div class="wrap-login100">
                <SetUser  reset_userinf={()=>this.setState({user_inf_rerand:true})} handleSetuser={(event)=>this.handleSetuser(event) } handleChange={(event)=>this.handleChange(event)} state={this.state}/>
                </div>
                <h2> testing_account:<tr></tr>player</h2>
                <h2> testing_key:5HxM6iGfYDYTLHDgRruBhbzzSsxndxfLXXswq4C1AS8gkLVGz3E</h2>
                </div>
                
                );
            case "About":
                return this.wraped(<About gosetuser={()=>this.setState({userin:"setuser"} )}  givememoney={this.handleAskmoney}/>)
            case "Infs":
                return this.wraped(<Infs/>)
            case "bet":
                return this.wraped(<ActionBet handleChange={(event)=>this.handleChange(event)} state={this.state} reset_userinf={()=>this.setState({user_inf_rerand:true})}/>);
            case "mytickets":
                return this.wraped(<ConsoleOfTable reset_userinf={()=>this.setState({user_inf_rerand:true})}/>);
            case "redeem":
                return this.wraped(<ActionRedeem handleChange={(event)=>this.handleChange(event)} state={this.state} reset_userinf={()=>this.setState({user_inf_rerand:true})}/> );
            default:
                return (
                    <main></main>
                );
        };
        
        
    }
}
class  Consoleblock extends React.Component {
    render(){
    return(
            <h3 id="CONTEX">
                {this.props.value}
            </h3>
        )
    }
}



ReactDOM.render(
    <Manage />,
    document.getElementById('root')
);


