import { Nav,Button,Modal,Spinner} from 'react-bootstrap';
import React from 'react';




class Popoutwindow extends React.Component {
    
    constructor(props){
        super(props);
        
        
    }
    render(){
    if(! this.props.show){
      return(<div></div>)
    }
    return (
      <>
        <Modal show={this.props.show} onHide={this.props.handleClose}>
          <Modal.Header closeButton>
            <Modal.Title>{this.props.title}</Modal.Title>
          </Modal.Header>
          <Modal.Body>{process_transection_obj(this.props.console_obj)}</Modal.Body>
          <Modal.Footer>
            <Button variant="primary" onClick={this.props.handleClose}>
            Close
            </Button>
          </Modal.Footer>
        </Modal>
      </>
    );
  }
}
function process_transection_obj(transection) {
      if(transection.passinf=='Loading'){
        return(
          <Spinner animation="border" role="status">
            <span className="sr-only">Loading...</span>
          </Spinner>
        );
      }
      if(transection.passinf=='error'){
        return(
          <>
          <p>error</p>
          <p>{transection.errormessage}</p>
          </>
        );
      }
      if(transection.passinf=='success'){
        return(
          <>
          <h4>Success</h4>
          
          </>
        );
      }
      return(
        <>
        <h4>Transection success</h4>
        <div>{transection.action_traces[0].block_time}</div>
        <div>
          {JSON.stringify(transection.action_traces[0].act.data)}
        </div>
        <div className="upp">receiver :{transection.action_traces[0].receiver}</div>
        <div className="upp">Inline actions: </div>
        <div className="inlinetraces wwbw upp">{JSON.stringify(transection.action_traces[0].inline_traces)}</div>
        </>
      );
}
export default Popoutwindow;
