import React from 'react';
import ApiService from './API';
import Popoutwindow from './popoutwindow'
import { Form,Button,} from 'react-bootstrap';
import ActionCreate from './Creatuser';
import img_chain from './ChainDiagram.png'
class About extends React.Component{
    constructor(props){
        super(props);
        this.state={
          pop_show:false,
        }
        
    }
    howtoplay=(
        <div className="information_block w90 up4p mc ">
            <div className="al upp"><h2>Before You Play</h2></div>
            <div className="flex_row"><h5>Set User</h5>&emsp; Input private key and account name at &thinsp; <a href="#" onClick={this.props.gosetuser}>"SET USER" &thinsp;</a> </div>
            <div className="flex_row"><h5>Creat User</h5>&emsp; Creat your own account ( Note: An account can only creat my another account) &thinsp;</div>
            <Button variant="light" onClick={()=>this.setState({pop_show:true})}>Click me to create! </Button>
            <div className="flex_row"><h5>No Token?</h5> &thinsp;</div>
            <Button variant="dark" onClick={this.props.givememoney}>GIVE ME TOKEN! </Button>

        </div>
    )
    render(){
        return(
            <>
            
            <ActionCreate show={this.state.pop_show} title={"Creat User"} handleClose={()=>this.setState({pop_show:false})}/>
            <div className="about w60 mc">
            {head_img}
            {this.howtoplay}
            
            {account}
            {prize_number}
            {dealer}
            {muti_index_table}
            {player_action}
            </div>
            </>
        )
    }
}

const head_img=(

    <div className="information_block w90 up4p mc ac">
        <div className="al upp"><h2>This Webpage</h2></div>
        <div className="flex_row ">
        <div className="al w50"><p>This web page is an userinterface to smart contract on block chain. All process will be finish in chain</p>
            <a href="https://www.vlottery.tw/v1/chain/get_info">Click me to get chain information</a>
        </div>
        <img src={img_chain} />
        </div>
    </div>
)
const bet=(
    <div className="information_block w90 up4p mc" id="inf_title">
        <div className="al upp"><h2>Bet</h2></div>
        <div className="al w60"><p>Transfer the token:"TNT" form your account to the smart contract to buy the tickets </p></div>
        
    </div>
)
const smartcontract=(
    <div className="information_block w90 up4p mc" id="inf_title">
        <div className="al upp"><h2>Smart Contract</h2></div>
        <div className="al w60"><p>A smart contract combines a legal contract with code to automate the execution of specific terms of an agreement between parties in an objective way. You can see it as a program on the account, and executed by block chain VM.</p></div>
        
    </div>
)
const account=(
    <div className="information_block w90 up4p mc" id="inf_title">
        <div className="al upp"><h2>Account</h2></div>
        <div className="al w60"><p>An account is the unit of user, EOSIO will mannage recoruse by account. You can see a account as a user, dosen't mater it is dominate by a contract or real people</p></div>
        
    </div>
)
const prize_number=(
    <div className="information_block w90 up4p mc" id="inf_title">
        <div className="al upp"><h2>Prize number</h2></div>
        <div className="al w60">
            <p>
            The prize number will be caculate by hashing current time ,an unvisble secrete seed and all user's TNT amount in their account. While, the secret seed 
            will be changed every sungle bet, so it can prevent dealer or player cheat in the game.
            </p>
        </div>
        
    </div>
)
const dealer=(
    <div className="information_block w90 up4p mc" id="inf_title">
        <div className="al upp"><h2>Dealer </h2></div>
        <div className="al w60">
            <p>
             Dealer is the account who prepare the prize. The account is wrote in the contract, it has the premision to initall the game 
             and shut down the contract account. But it doesn't have any more premision like tranfom the token in account.
            </p>
        </div>
        
    </div>
)
const muti_index_table=(
    <div className="information_block w90 up4p mc" id="inf_title">
    <div className="al upp"><h2>Muli Index Table </h2></div>
    <div className="al w60">
        <p>place to store the data of the contract</p>
        <p>
            As the contract will be loaded before it executed everytime, so the data must be store in another place (you can see it as a data base). 
            The multi index table can be search without any promition, so the contract  is tranceperit to every user.
        </p>
    </div>
    
</div>
)
const player_action=(
    <div className="information_block w90 up4p mc">
        <div className="al upp"><h2>Player Actions </h2></div>
        <div className="action_name upp">
            <h4>Bet</h4>
            <div className="">Player transform tokens to the lottery smart contract with numbers and period in memo. The 
                smart contract will store it in multi index table.
            </div>
        </div>
        <div className="action_name upp">
            <h4>Redeem</h4>
            <div className="">
                Player push acation with ticket ID, the smart contract will check the prize and send the token
                . After finished above smart contract will erase the ticket in the table. 
            </div>
        </div>
        <div className="action_name upp">
            <h4>RedeemAll</h4>
            <div className="">
                Player push action with players name, the smart contract will check all tickets belongs to player
                and send the token. After finished above smart contract will erase the ticket in the table
            </div>
        </div>
        <div className="action_name upp">
            <h4>Refund</h4>
            <div className="">
                Player push acation with ticket ID, the smart contract will send the token back and erase the ticket 
                on the table.
            </div>
        </div>

    </div>
)
export default About;