import React from 'react';
import ApiService from './API';

class  GetCurrentPeriod extends React.Component {
    constructor(props){
        super(props);
        this.state = {
            console_block_string:'',
            returnobj:new Object(),
            finded:false,
        }
        ApiService.get_allow_table()
        .then((mesg) => {
            console.dir(mesg);
            
            //this.setState({console_block_string:JSON.stringify(mesg.rows)})
            this.setState({console_block_string:'Next period :'.concat(mesg.rows[0].period)});
            this.setState({finded:true});
        })
        .catch(err => {
            console.dir(err);
            this.setState({console_block_string:"some thing was wrong please try it again"});
        });
        
    }
    render(){
        return(
            <div class="allow_table">
                {this.state.console_block_string}
            </div>
        );
    }
}
export default GetCurrentPeriod;