import React from 'react';
import img_form_icon from "./18.jpg";
import { Form,Button,} from 'react-bootstrap';
import Popoutwindow from './popoutwindow';
import ApiService from './API';

class SetUser extends React.Component{
    constructor(props) {
        super(props);

        this.state={
            pop_show:false,
            pop_console:'',
            returnobj:{passinf:'Loading'},
            current_balance_rerender:true,
        }
        this.handleSetuserlo=this.handleSetuserlo.bind(this);
      }
    handleSetuserlo(event){
        const { form } = this.props.state;
        sessionStorage.setItem("User_EOS_key", form.key);
        sessionStorage.setItem("User_EOS_account", form.username);
        event.preventDefault();
        
        this.setState({returnobj:{passinf:'Loading'}});
        this.setState({pop_show:true});
        return ApiService.check_user()
            .then((mesg) => {
                console.dir(mesg);
                this.setState({returnobj:{passinf:'success'},current_balance_rerender:true})
                this.props.reset_userinf();
                
            })
            .catch(err => {
                console.dir(err);
                
                this.setState({returnobj:{passinf:'error',errormessage:err.message}})
                
            });
    }
    render(){
        const { form, error } = this.props.state;
        return (
            <>
            <Popoutwindow show={this.state.pop_show} title={"Checking user"} handleClose={()=>this.setState({pop_show:false})} console_obj={this.state.returnobj}/>
            <span class="form-icon">
                    <img src={img_form_icon} />
            </span>
            <Form name="form" onSubmit={this.handleSetuserlo}>
                <Form.Group >
                    <Form.Label>Account name</Form.Label>
                    <Form.Control  name="username" type="text" placeholder="All small letters, a-z, 1-5 or dot, max 12 characters" onChange={this.props.handleChange} pattern="[\.a-z1-5]{2,12}" required autoComplete="off"/>
                </Form.Group>
                <Form.Group >
                    <Form.Label>Private key</Form.Label>
                    <Form.Control name="key" type="password" value={form.key} onChange={this.props.handleChange} pattern="^.{51,}$" required autoComplete="new-password"/>
                </Form.Group>
                <Button variant="primary" type="submit">
                    Set  User and Key
                </Button>       
            </Form>
            </>
        );



    }
}

export default SetUser;
