import React from 'react';
import ApiService from './API';
import { Badge } from 'react-bootstrap';

class GetUserInf extends React.Component{
    constructor(props) {
        super(props);
        this.state = {
            console_block_string:'',
            returnobj:new Object(),
            finded:false,
            setteduser:'',
        }
        
      }
    render(){
        if(this.props.user_inf_rerand){
            if(this.state.finded ){
                this.setState({finded:false});
            }
            this.props.reranded_inf();
        }
        if(!(sessionStorage.getItem("User_EOS_account"))){
            if(this.state.finded) {
                this.setState({finded:false});
            }
            return(<p>{this.state.console_block_string}</p>);
        }
        if((sessionStorage.getItem("User_EOS_account")!=this.state.setteduser)){
            if(this.state.finded) {
                this.setState({finded:false});
            }
            this.setState({setteduser:sessionStorage.getItem("User_EOS_account")})
        }
        
        if(! this.state.finded ){
            this.setState({finded:true});
            
            this.setState({setteduser:sessionStorage.getItem("User_EOS_account")})
            ApiService.get_token().then((mesg) => {
                console.dir(mesg);
                //this.setState({console_block_string:JSON.stringify(mesg.rows)})
                try {
                    
                    this.setState({console_block_string:mesg.rows[0].balance});
    
                }catch (err){
                    console.dir(err);
                    this.setState({console_block_string:"some thing was wrong "});
                }
            })
            .catch(err => {
                console.dir(err);
                this.setState({console_block_string:"some thing was wrong "});
            });
        }
        if(this.state.finded){
            return(
                <div class="user_inf ac">
                    <Badge variant="info">User Name</Badge>
                    {sessionStorage.getItem("User_EOS_account")}
                    {' '}
                    <Badge variant="info">Token</Badge>
                    {this.state.console_block_string}
                    
                    
                </div>
            );
        }
        else{
            
            return(<p>{this.state.console_block_string}</p>);
        }


    }

}
export default GetUserInf;
