import React from 'react';
import ApiService from './API';
import Popoutwindow from './popoutwindow'
import { Form,Button,} from 'react-bootstrap';
import {Table,Card,Jumbotron,Container,Badge} from 'react-bootstrap';
import img_lotto from"./lotto_boll.jpg"
import Infs from "./Infs"


class  ActionRedeem extends React.Component {
    constructor(props) {
        // Inherit constructor
        super(props);
        // State for form data and error message
        
        // Bind functions
        this.state={
            pop_show:false,
            pop_console:'',
            returnobj:new Object(),
            current_balance_rerender:true,
        }
        
        this.handleSubmit = this.handleSubmit.bind(this);
        this.redeemALL=this.redeemALL.bind(this);
    }
    handleSubmit(event) {
        
        event.preventDefault();
        const { form } = this.props.state;
        const ID = form.gameid
        this.setState({returnobj:{passinf:'Loading'},pop_show:true})
        
        return ApiService.open(ID)
            .then((mesg) => {
                console.dir(mesg);
                this.setState({returnobj:mesg.processed})
                this.props.reset_userinf();
            })
            .catch(err => {
                console.dir(err);
                
                this.setState({returnobj:{passinf:'error',errormessage:err.message}})
                this.props.reset_userinf();
            });
    }
    redeemALL(event) {
        
        event.preventDefault();
        
        this.setState({returnobj:{passinf:'Loading'},pop_show:true})
        
        return ApiService.redeemall()
            .then((mesg) => {
                console.dir(mesg);
                this.setState({returnobj:mesg.processed})
                this.props.reset_userinf();
            })
            .catch(err => {
                console.dir(err);
                
                this.setState({returnobj:{passinf:'error',errormessage:err.message}})
                this.props.reset_userinf();
            });
    }

    render(){
        if(! sessionStorage.getItem("User_EOS_key")){
            return(
                <p>please set user</p>
            );
        }
        else{
            const { form, error } = this.props.state;
            return(
                <>
                <div className="w80 mc upp">{titleblocks}</div>
                <div id="redeem_block" className="w50 mc">
                <Popoutwindow show={this.state.pop_show} title={"Transaction Information"} handleClose={()=>this.setState({pop_show:false})} console_obj={this.state.returnobj}/>
                <Form onSubmit={this.handleSubmit}>
                    <Form.Group >
                        <Form.Label>ID</Form.Label>
                        <Form.Control name="gameid" type="string" placeholder="your ticket ID is?" autoComplete="off" onChange={this.props.handleChange} value={form.gameid}/>
                        <Form.Text className="text-muted">
                            Input your ticket ID, or you can redeem tickets in "My Tickets" 
                        </Form.Text>
                    </Form.Group>
                    <Button variant="primary" type="submit">
                        Redeem
                    </Button>
                    <Form.Group className="upp">
                        <Form.Label >
                            Redeem all your tickets
                        </Form.Label>
                    </Form.Group>
                    <Button variant="primary" onClick={this.redeemALL}>
                        Redeem all
                    </Button>
                </Form>
                </div>
                <Infs current_balance_rerender={this.state.current_balance_rerender} rended={()=>this.setState({current_balance_rerender:false})} rerend={()=>this.setState({current_balance_rerender:true})}/>
                </>
            );
        }
    }
}
const bgGround_lotto= {
    backgroundImage: 'url(' +img_lotto + ')'//圖片的路徑
  };
const titleblocks=(
    
    <Jumbotron fluid className="bg-dark text-white" style={bgGround_lotto}>
        <Container className=" w80">
            <h1 className="al">Redeem Your Tickets</h1>
            <div className="frow mb">
            
            
            
            </div>
            
    </Container>
    </Jumbotron>
    
)
export default ActionRedeem;